.vux-loading {
  -webkit-animation-duration: 0.6s;
          animation-duration: 0.6s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: vux-loading;
          animation-name: vux-loading;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  border-radius: 99em;
  border: 3px solid #ddd;
  border-left-color: #666;
  display: inline-block;
  width: 16px;
  height: 16px;
  border-width: 2px;
  display: table-cell;
  vertical-align: middle;
}
.weui_toast {
  position: fixed;
  z-index: 50000;
  width: 7.6em;
  min-height: 7.6em;
  top: 180px;
  left: 50%;
  margin-left: -3.8em;
  background: rgba(40,40,40,0.75);
  text-align: center;
  border-radius: 5px;
  color: #fff;
}
.weui_icon_toast {
  margin: 22px 0 0;
  display: block;
}
.weui_icon_toast:before {
  content: '\EA08';
  color: #fff;
  font-size: 55px;
}
.weui_toast_content {
  margin: 0 0 15px;
}
.weui_loading_toast .weui_toast_content {
  margin-top: 64%;
  font-size: 14px;
}
.weui_loading {
  position: absolute;
  width: 0px;
  z-index: 1;
  left: 50%;
  top: 38%;
}
.weui_loading_leaf {
  position: absolute;
  top: -1px;
  opacity: 0.25;
}
.weui_loading_leaf:before {
